import React from "react";

const GardenPlan = () => {
  return (
    <div className="info">
      <div className="title">
        <h1>Plan Twojego ogrodu</h1>
      </div>
      <div className="scrollBox">
        <p>
          Funkjonalno uatwijca uytkownikom aplikacji zaprojektowanie ogrodu
          i/lub jego poszczeglnych czci. Majc do dyspozycji proste narzdzia
          do sporzdzania schematycznych projektw, uytkownik bdzie mg
          zaaranowa w aplikacji ksztat swojego ogrodu. Funkcjonalo nie jest
          jeszcze wdroona.
        </p>
        <p className="slogan-green">
          Zao konto! Korzystaj z zasobw i narzdzi gardenbook'a.
        </p>
      </div>
    </div>
  );
};

export default GardenPlan;
