import React from "react";

const UsefulTools = () => {
  return (
    <div className="info">
      <div className="title">
        <h1>Przydatne narzdzia</h1>
      </div>
      <div className="scrollBox">
        <p>
          gardenbook oferuje swoim uytkownikom wiele przydatnych narzdzi,
          uatwiajcych organizacj prac ogrodniczych i zarzdzajcych.
          Podstawowe narzdzia to: planer prac, baza rolin i materiaw. W
          planie do wdroenia znajduj si: statystyki, historia, plan ogrodu
          oraz wyszukiwarka wykonawcw prac ogrodniczych w Twoim pobliu.
        </p>
        <p className="slogan-green">
          Zao konto! Korzystaj z zasobw i narzdzi gardenbook'a.
        </p>
      </div>
    </div>
  );
};

export default UsefulTools;
