import React from "react";

const Help = () => {
  return (
    <div className="user-content">
      <div className="user-right-box">
        <h2>Pomoc</h2>
        <p>
          W tym dziale znajd si wskazwki w jaki sposb korzysta z aplikacji.
          Jak dodawa nowe elementy do aplikacji, w jaki sposb zarzdza jej
          funkcjonalnoci.
        </p>
      </div>
    </div>
  );
};

export default Help;
