import React from "react";

const ExistedMaterialsListHead = () => {
  return (
    <>
      <tr>
        <td colSpan="6">
          <h3>Lista materiaw</h3>
        </td>
      </tr>
      <tr>
        <th className="shortTd">Lp.</th>
        <th className="longTd">Nazwa</th>
        <th className="longTd">Zdjcie</th>
        <th className="longTd">Ilo</th>
        <th>Jedn.</th>
        <th className="longTd">Cena jedn. [z.]</th>
        <th className="longTd">Warto [z.]</th>
        <th>Edytuj</th>
        <th>Usu</th>
      </tr>
    </>
  );
};

export default ExistedMaterialsListHead;
