import React from "react";
import logo from "../../assets/img/lotus.png";

const Info = () => {
  return (
    <div className="right-box">
      <div className="title">
        <img id="logo" src={logo} alt="gardenbook logo" />
        <h1>Ogrodniczy serwis spoecznociowy</h1>
      </div>
      <ul>
        <li>
          <span className="important">umoliwia</span> atw wymian wiedzy i
          dowiadcze
        </li>
        <li>
          <span className="important">uatwia</span> nawizanie kontaktw z
          innymi ogrodnikami
        </li>
        <li>
          <span className="important">udostpnia</span> narzdzia przydatne w
          zarzdzaniu ogrodem
        </li>
      </ul>
      <p>Wszystko w jednym miejscu!</p>
      <p className="slogan-green">
        Zao konto! Korzystaj z zasobw i narzdzi gardenbook'a.
      </p>
    </div>
  );
};

export default Info;
