import React from "react";

const FindContractor = () => {
  return (
    <div className="info">
      <div className="title">
        <h1>Znajd wykonawc</h1>
      </div>
      <div className="scrollBox">
        <p>
          Dziki tej funkcjonalnoci uytkownicy bd mogli w atwy sposb
          wyszuka w swoim pobliu wykonawcw interesujcych ich prac
          ogrodniczych. Ta funkcjonalno nie jest jeszcze wdroona.
        </p>
        <p className="slogan-green">
          Zao konto! Korzystaj z zasobw i narzdzi gardenbook'a.
        </p>
      </div>
    </div>
  );
};

export default FindContractor;
