import React from "react";

const DataMagazine = () => {
  return (
    <div className="info">
      <div className="title">
        <h1>Magazyn Danych</h1>
      </div>
      <div className="scrollBox">
        <p>
          gardenbook oferuje swoim uytkownikom magazynowanie danych dotyczcych
          iloci rolin znajdujcych si w ogrodzie oraz materiaw znajdujcych
          si na stanie. Dziki temu w atwy sposb mona zarzdza zasobami
          ogrodu - szybko mona ustali jakie elementy naley uzupeni.
        </p>
        <p className="slogan-green">
          Zao konto! Korzystaj z zasobw i narzdzi gardenbook'a.
        </p>
      </div>
    </div>
  );
};

export default DataMagazine;
