import React from "react";

const KnowledgeExchange = () => {
  return (
    <div className="info">
      <div className="title">
        <h1>Wymiana wiedzy</h1>
      </div>
      <div className="scrollBox">
        <p>
          Dziki wbudowanemu w aplikacj portalowi spoecznociowemu uytkownicy
          bd mogli w atwy sposb wymienia si wiedz, doswiadczeniem.
          Pozowli to poszczeglnym uytkownikom na poszerzenie swojej wiedzy i
          skorzystanie z dowiadcze innych ogrodnikw, stanowicych spoeczno
          gardenbook'a.
        </p>
        <p className="slogan-green">
          Zao konto! Korzystaj z zasobw i narzdzi gardenbook'a.
        </p>
      </div>
    </div>
  );
};

export default KnowledgeExchange;
