import React from "react";

const GardensCommunity = () => {
  return (
    <div className="info">
      <div className="title">
        <h1>Spoeczno Ogrodnikw</h1>
      </div>
      <div className="scrollBox">
        <p>
          Docelowa funkcjonalno gardenbook'a to moliwo komunikacji z innymi
          uytkownikami aplikacji - wymiana dowiadcze, moliwo zadawania
          pyta, itp. Umoliwi j wbudowany w aplikacj portal spocznociowy
          dla uytkownikw. Uytkownicy aplikacji sami zdecyduj, czy bd
          korzysta z tej funkcjonalnoci. Ta funkcjonalno nie jest jeszcze
          wdroona.
        </p>
        <p className="slogan-green">
          Zao konto! Korzystaj z zasobw i narzdzi gardenbook'a.
        </p>
      </div>
    </div>
  );
};

export default GardensCommunity;
