import React from "react";

const ExistedPlantsListHead = () => {
  return (
    <>
      <tr>
        <td colSpan="11">
          <h3>Lista rolin</h3>
        </td>
      </tr>
      <tr>
        <th className="shortTd">Lp.</th>
        <th className="longTd">Nazwa</th>
        <th className="longTd">Zdjcie</th>
        <th>
          Ilo
          <br />
          [szt.]
        </th>
        <th>
          Cena jedn.
          <br />
          [z.]
        </th>
        <th>
          Warto
          <br />
          [z]
        </th>
        <th>Nawz</th>
        <th>
          Dawka
          <br />
          [ml lub gr]
        </th>
        <th colSpan="2" className="longTd">
          Czstoto
          <br /> nawoenia / podlewania
          <br />
          [na rok] / [na tydz.]
        </th>
        <th>Edytuj</th>
        <th>Usu</th>
      </tr>
    </>
  );
};

export default ExistedPlantsListHead;
